<template>
  <notice title="VRデバイスに接続できませんでした" :noticeActive="isNoticeActive" />
  <div class="course-img">
    <img :src="image" alt="cookImg" />
    <button type="button" v-if="isPlayButton" @click="playVR">
      <span>{{ $t('course-detail.VR-play-btn') }}</span>
      <font-awesome-icon :icon="castIcon" class="icon" />
    </button>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, ref, SetupContext } from 'vue'
import { faChromecast } from '@fortawesome/free-brands-svg-icons'
import { useStore } from 'vuex'
import Notice from '@/components/Notice.vue'

export default defineComponent({
  components: {
    Notice
  },
  props: {
    image: {
      type: String,
      required: true
    },
    isPlayButton: {
      type: Boolean,
      required: false,
      default: true
    },
    videoData: {
      type: Object
    }
  },
  emits: ['device-link-popup-active', 'set-modal-active'],
  setup(props, context: SetupContext) {
    const isNoticeActive = ref(false)
    const store = useStore()
    const setLoading = (value: boolean) => {
      store.commit('setLoading', value)
    }
    const noticeActive = () => {
      isNoticeActive.value = true
      setTimeout(() => (isNoticeActive.value = false), 1500)
    }
    const connectingBluetoothDeviceMacAddress = computed(() => store.state.connectingBluetoothDevice.macAddress)
    const playVR = () => {
      if (connectingBluetoothDeviceMacAddress.value) {
        setLoading(true)
        setTimeout(async () => {
          const isBtResult = await store
            .dispatch('submitBTDeviceData', {
              videoData: JSON.stringify(props.videoData)
            })
            .then((result) => result)
          setLoading(false)
          if (isBtResult === 'true') {
            context.emit('set-modal-active', true)
            return
          } else {
            //  TODO falseの処理
            noticeActive()
            context.emit('device-link-popup-active', true)
          }
        }, 100)
      } else {
        context.emit('device-link-popup-active', true)
      }
    }

    const castIcon = computed(() => faChromecast)

    return {
      castIcon,
      playVR,
      isNoticeActive
    }
  }
})
</script>

